body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-manrope: Manrope;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-lg: 18px;
  --font-size-3xl: 22px;
  --font-size-mini: 15px;

  /* Colors */
  --color-white: #fff;
  --color-slategray: #565973;
  --color-mediumslateblue: #6366f1;

  /* Gaps */
  --gap-mini: 15px;
}
